import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {

  peopleByGroup : any[] = [
    {
      country:'Saudi',
      names:[
        {name:'Sarah'},
        {name:'Maha'},
        {name:'Adel'},
        {name:'Mohemed'}
      ]
    },
    {
      country:'Egept',
      names:[
        {name:'Ali'},
        {name:'Ahmid'},
        {name:'Naser'},
        {name:'Jmal'}
       ]
    }
  ];

  courses = [
    'Java',
    'C#',
    'Css'
  ]

  onAdd(){
    this.courses.push('Javascript');
  }

  onRemove(c){
    let index = this.courses.indexOf(c);
    this.courses.splice(index,1)
  }
}
